    }\r\n\r\n    .nav-items.active {\r\n        position: static;\r\n        display: flex;\r\n        flex-direction: column;\r\n        align-items: center;\r\n        gap: 0.5rem;\r\n        animation-name: nav-active;\r\n        animation-duration: 0.5s;\r\n    }\r\n\r\n    .main-page{\r\n        flex-direction: column;\r\n    }\r\n\r\n    .restoItem{\r\n        flex-direction: column;\r\n        flex-wrap: wrap;\r\n        justify-content: center;\r\n        align-items: center;\r\n        gap: 2rem;\r\n        margin: 1rem 0;\r\n    }\r\n\r\n    .resto{\r\n        width: 40vh;\r\n    }\r\n\r\n    .img-resto{\r\n        width: 100%;\r\n    }\r\n\r\n    /* Detail Item Mobile */\r\n    .detail-top >img{\r\n        margin-bottom: 20px;\r\n        width: 100%;\r\n    }\r\n\r\n    .detail-top{\r\n        flex-direction: column;\r\n    }\r\n\r\n    .detail-dsc>h3{\r\n        font-size: 16px;\r\n    }\r\n\r\n    .detail-dsc>p{\r\n        font-size: 12px;\r\n    }\r\n\r\n    .menus-resto{\r\n        flex-direction: column;\r\n        gap: 10px;\r\n    }\r\n    #foodMenuList>h3, #drinkMenuList>h3{\r\n        font-size: 16px;\r\n    }\r\n\r\n    #foodMenuList>p, #drinkMenuList>p{\r\n        font-size: 12px;\r\n    }\r\n\r\n    .like{\r\n        width: 100%;\r\n        height: 50px;\r\n    }\r\n\r\n    /* about Item Mobile */\r\n    .about-list{\r\n        flex-direction: column;\r\n        align-items: center;\r\n    }\r\n\r\n    .about-container{\r\n        width: 90%;\r\n    }\r\n\r\n    .img-partner{\r\n        flex-direction: column;\r\n        align-items: center;\r\n    }\r\n}\r\n\r\n@media screen and (min-width: 600px) and (max-width: 990px){\r\n    nav {\r\n        display: flex;\r\n        flex-direction: column;\r\n        align-items: center;\r\n        padding: 5%;\r\n        background-color: #fff;\r\n    }\r\n    \r\n    .nav-proper {\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: center;\r\n        width: 100%;